.todoline {
  width: 100%;
  height: 24px;
  line-height: 40px; }
  .todoline:after {
    content: '';
    display: none;
    clear: both; }
  .todoline .line {
    float: left;
    margin-top: 19px;
    width: 457px;
    height: 2px;
    background-color: #dcdcdc; }
  .todoline .clear {
    float: right;
    width: 178px;
    font-size: 12px;
    text-align: center;
    color: #c2c2c2; }
    .todoline .clear:hover {
      cursor: pointer; }
    .todoline .clear .icon-delete {
      margin-right: 4px; }



