.todoItem {
  margin-top: 10px;
  margin-bottom: 35px;
  width: 100%;
  height: 68px;
  line-height: 48px; }
  .todoItem:after {
    content: '';
    display: block;
    clear: both; }
  .todoItem .content {
    position: relative;
    margin-left: 50px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out; }
    .todoItem .content:hover {
      background-color: #f2f2f2;
      border-radius: 3px; }
      .todoItem .content:hover .btn {
        opacity: 1; }
        .todoItem .content:hover .btn span {
          cursor: pointer; }
  .todoItem .common {
    float: left;
    margin-top: 8px;
    width: 26px;
    height: 26px;
    text-align: center;
    line-height: 26px;
    border-radius: 50%;
    position: relative;
    border: 2px solid #cbcbcb;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out; }
    .todoItem .common:hover {
      background-color: #d9d9d9;
      cursor: pointer; }
    .todoItem .common.iconfont {
      color: #fff; }
    .todoItem .common.completed {
      background-color: #7ed61f;
      border-color: #7ed61f;
      color: #fff; }
  .todoItem .text {
    position: relative;
    color: #000107;
    line-height: 48px;
    font-size: 18px; }
    .todoItem .text.completed {
      color: #cccacb;
      text-decoration: line-through; }
    .todoItem .text .point {
      position: absolute;
      top: 10px;
      margin-left: 3px;
      width: 10px;
      height: 10px;
      border-radius: 50%; }
      .todoItem .text .point.normal {
        background-color: #5ea6fa; }
      .todoItem .text .point.important {
        background-color: #f8a32c; }
      .todoItem .text .point.urgent {
        background-color: #f21d33; }
      .todoItem .text .point.someday {
        background-color: #cccccc; }
  .todoItem .btn {
    position: absolute;
    top: 0;
    right: 10px;
    opacity: 0;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear; }
    .todoItem .btn span {
      vertical-align: middle;
      color: #bcbcbc;
      font-size: 18px; }
    .todoItem .btn .icon-delete1 {
      position: relative;
      top: -2px;
      margin-left: 5px; }



