.input-wrapper {
  display: block;
  position: relative;
  margin-bottom: 15px;
  padding-top: 20px; }
  .input-wrapper input {
    width: 291px;
    line-height: 34px;
    padding-left: 10px;
    padding-right: 30px;
    font-size: 14px;
    border-bottom: 2px solid #98a3b7;
    background-color: #fff;
    color: #808aa5; }
  .input-wrapper .iconfont {
    position: absolute;
    top: 29px;
    right: 10px;
    color: #8b93aa; }
    .input-wrapper .iconfont.icon-mima {
      font-weight: 700; }
  .input-wrapper .desc {
    position: absolute;
    top: 20px;
    left: 10px;
    font-size: 22px;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    color: #808aa5; }
    .input-wrapper .desc.active {
      -webkit-transform: translateY(-20px);
          -ms-transform: translateY(-20px);
              transform: translateY(-20px);
      font-size: 14px;
      color: #bfc2d3; }



